
@font-face {
  font-family: 'DINPro';
  src: url('../fonts/DINPro.eot'); /* IE 9 Compatibility Mode */
  src: url('../fonts/DINPro.eot?#iefix') format('embedded-opentype'), /* IE < 9 */
    url('../fonts/DINPro.woff') format('woff'), /* Firefox >= 3.6, any other modern browser */
    url('../fonts/DINPro.ttf') format('truetype'); /* Safari, Android, iOS */
font-weight: 400;
    font-style: normal;
font-display: swap;
}

